.symbol-section {
  padding-top: 100px;
  padding-bottom: 100px;
}
@media screen and (min-width: 1025px) {
  .symbol-section.about-page-section {
    padding-top: 8.385vw;
  }
}
@media screen and (min-width: 1921px) {
  .symbol-section.about-page-section {
    padding-top: 161px;
  }
}
.symbol-section.careers-symbol {
  padding-top: 100px;
  padding-bottom: 100px;
}
@media screen and (max-width: 1024px) {
  .symbol-section .txt-content {
    width: 100%;
  }
}
.symbol-section .symbol-wrapper {
  text-align: center;
}
.symbol-section .symbol-section-svg {
  flex: 0 0 auto;
  margin-bottom: 30px;
  max-height: 530px;
}
@media screen and (min-width: 1025px) {
  .symbol-section .symbol-section-svg {
    padding-right: 35px;
  }
}
.symbol-section .txt-size-12 {
  margin-bottom: 15px;
}
@media screen and (min-width: 1025px) {
  .symbol-section .txt-size-12 {
    margin-bottom: 1.51vw;
  }
}
@media screen and (min-width: 1921px) {
  .symbol-section .txt-size-12 {
    margin-bottom: 29px;
  }
}
.symbol-section .txt-size-80 {
  margin-bottom: 25px;
}
@media screen and (min-width: 1025px) {
  .symbol-section .txt-size-80 {
    margin-bottom: 2.604vw;
  }
}
@media screen and (min-width: 1921px) {
  .symbol-section .txt-size-80 {
    margin-bottom: 50px;
  }
}
.symbol-section .txt-post-rtf {
  margin-bottom: 25px;
}
@media screen and (min-width: 1025px) {
  .symbol-section .txt-post-rtf {
    margin-bottom: 2.5vw;
    padding-right: 1.823vw;
  }
}
@media screen and (min-width: 1921px) {
  .symbol-section .txt-post-rtf {
    margin-bottom: 48px;
    padding-right: 35px;
  }
}
@media screen and (max-width: 550px) {
  .symbol-section .txt-post-rtf p:not(:last-of-type) {
    margin-bottom: 4vw;
  }
}
@media screen and (min-width: 551px) and (max-width: 1024px) {
  .symbol-section .txt-post-rtf p:not(:last-of-type) {
    margin-bottom: 20px;
  }
}
@media screen and (min-width: 1025px) {
  .symbol-section .txt-post-rtf p:not(:last-of-type) {
    margin-bottom: 1.615vw;
  }
}
@media screen and (min-width: 1921px) {
  .symbol-section .txt-post-rtf p:not(:last-of-type) {
    margin-bottom: 31px;
  }
}
.symbol-section .max-w-container {
  display: flex;
  align-items: flex-start;
}
@media screen and (max-width: 1024px) {
  .symbol-section .max-w-container {
    flex-flow: column nowrap;
  }
}
.symbol-section .st0 {
  fill: none;
}
.symbol-section .st1 {
  fill: #002fa7;
}
.symbol-section .st2 {
  fill: #17fcc5;
}
.symbol-section .st3 {
  fill: #008fc5;
}
.symbol-section .st4 {
  fill: #fe0249;
}